import one from "./one"
import two from "./two"
import three from "./three"
export default {
	components: {
		one,
		two,
		three
	},
	props: {},
	data() {
		return {
			list: [], //轮播
			indicatorDots: true,
			autoplay: true,
			interval: 5000,
			duration: 500,
			suer: [], //超英
			lists:[]
		}
	},
	onLoad() {
		uni.request({
			url: `${this.$api}/index/carousel/list?qq=122212489`,
			method: 'POST',
			data: {},
			success: res => {
				this.list = res.data.data
				// console.log(this.list);
			},
			fail: (e) => {
				console.log(e);
			},
			complete: () => {}
		});
	},
	  onPullDownRefresh: function() {
	  uni.request({
	  	url:`${this.$api}/index/guessULike?qq=122212489`,
	  	method: 'POST',
	  	data: {},
	  	success: res => {
	  		this.lists = res.data.data
	  		// console.log(this.list);
			uni.stopPullDownRefresh()
	  	},
	  	fail: () => {},
	  	complete: () => {}
	  });
	  },
	methods: {
		like(){
			uni.request({
				url:`${this.$api}/index/guessULike?qq=122212489`,
				method: 'POST',
				data: {},
				success: res => {
					this.lists = res.data.data
					// console.log(this.list);
				},
				fail: () => {},
				complete: () => {}
			});
		}
	},
	mounted() {
		this.like()
	}
}
